.catalog {
  display: grid;
  justify-content: center;
  gap: 48px;
  margin: 0 auto;

  @media (min-width: 488px) {
    grid-template-columns: repeat(2, #{$width-card});
  }

  @media (min-width: 768px) {
    grid-template-columns: repeat(3, #{$width-card});
  }

  @media (min-width: 1024px) {
    grid-template-columns: repeat(4, #{$width-card});
  }

  &__card {
    display: flex;
    justify-content: center;
    align-items: center;
    width: #{$width-card};
    height: 300px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 48px;
  }
}
